import "./App.css";
import React, { useState, useEffect } from "react";
import Button from "react-bootstrap/Button";
// import Container from "react-bootstrap/lib/Container";
import { Container, Col, Row, ListGroup, ListGroupItem } from "react-bootstrap";
// import Row from "react-bootstrap/Row";

function Search({ setTitle }) {
  const onChange = (event) => {
    setTitle(event.target.value);
  };

  return (
    <div>
      <input
        onChange={onChange}
        type="text"
        id="textInput"
        placeholder="Movie Title"
      />
    </div>
  );
}

function Results({ title, page, setNominated, nominated, setPage }) {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!title) return;
    if (title.length >= 3) {
      setLoading(true);
      fetch(
        `https://www.omdbapi.com/?s=${title}&apikey=b29472dd&type=movie&page=${page}`
      )
        .then((response) => response.json())
        .then(setData)
        .then(() => setLoading(false))
        .catch(setError);
    }
  }, [page, title]);

  if (title.length < 3)
    return <p>Please enter a search query of at least 3 characters</p>;

  if (loading) return <p>Loading...</p>;

  if (error) return <pre>{JSON.stringify(error, null, 2)}</pre>;

  if (!data) return null;

  if (data.Response === "False") return <p>Movie Not Found</p>;

  return (
    <>
      <ListGroup className="list-unstyled">
        {data.Search.map((data) => (
          <ListGroup.Item key={data.imdbID}>
            <Container>
              <Row>
                <Col md="auto">
                  <Button
                    onClick={() =>
                      setNominated({ arr: [...nominated.arr, data] })
                    }
                    variant="outline-secondary"
                    size="sm"
                    name="imdbID"
                    value={data.imdbID}
                  >
                    Nominate
                  </Button>
                </Col>
                <Col>
                  {data.Title} ({data.Year})
                </Col>
              </Row>
            </Container>
          </ListGroup.Item>
        ))}
        <ListGroup.Item>
          <Button variant="outline-secondary" onClick={() => setPage(page + 1)}>
            Next Page
          </Button>
        </ListGroup.Item>
      </ListGroup>
    </>
  );
}

function Nominated({ nominated, setNominated }) {
  return (
    <>
      <ListGroup className="list-unstyled">
        {nominated.arr.map((movie) => (
          <ListGroup.Item key={movie.imdbID}>
            <Container>
              <Row>
                <Col md="auto">
                  <Button
                    onClick={() =>
                      setNominated({
                        arr: nominated.arr.filter((e) => e !== movie),
                      })
                    }
                    variant="outline-secondary"
                    size="sm"
                    name="imdbID"
                    value={movie.imdbID}
                  >
                    Remove
                  </Button>
                </Col>
                <Col>
                  {movie.Title} ({movie.Year})
                </Col>
              </Row>
            </Container>
          </ListGroup.Item>
        ))}
      </ListGroup>
    </>
  );
}

function App() {
  const [title, setTitle] = useState("");
  const [page, setPage] = useState(1);
  const [nominated, setNominated] = useState({ arr: [] });

  return (
    <>
      <Container>
        <Row>
          <Col>
            <Search setTitle={setTitle} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Results
              title={title}
              page={page}
              setPage={setPage}
              setNominated={setNominated}
              nominated={nominated}
            />
          </Col>
          <Col>
            <Nominated nominated={nominated} setNominated={setNominated} />
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default App;
